.wrapInputParam {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;

  input {
    width: 100px;
    margin-left: 10px;
    padding: 5px;
    color: #34495E;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid #6fb3ff;
    border-radius: 5px;
    outline:none;
  }

  .error {
    background-color: #ff6762;
  }

}


